.stocks {
  margin-bottom: 80px;
  @include width-1400 {
    margin-bottom: 70px;
  }
  @include width-1200 {
    margin-bottom: 60px;
  }
  &__item {
    &:not(:last-child) {
      margin-bottom: 100px;
      @include width-1400 {
        margin-bottom: 70px;
      }
      @include width-1200 {
        margin-bottom: 60px;
      }
    }
  }
  &__cards {
    margin-top: 50px;
    @include width-1200 {
      margin-top: 40px;
    }
  }
  &__btn {
    margin-top: 60px;
    @include width-1400 {
      margin-top: 32px;
    }
  }
}
