// -------------PRODUCT-------------
.product {
  margin-top: 50px;

  @include width-1200 {
    margin-top: 40px;
  }
  &__body {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;

    @include width-1400 {
      gap: 25px;
    }
    @include width-1200 {
      grid-template-columns: 1fr;
    }
    @include width-768 {
      gap: 16px;
    }
  }
  &__compass {
    position: absolute;
    width: 60px;
    height: 60px;
    top: 16px;
    right: 16px;
    z-index: 2;

    @include width-1200 {
      width: 50px;
      height: 50px;
      right: 0;
      top: 0;
    }
    @include width-768 {
      width: 40px;
      height: 40px;
    }
  }
  &__header {
    & a {
      position: static;
    }
  }
}


@keyframes infScale {
  0% {
    scale: 1;
  }
  100% {
    scale: 1.3;
  }
}


.product-img {
  position: relative;

  @include width-1200 {
    width: 100%;
    max-width: 480px;
    justify-self: center;
  }
  & .tabs__nav {
    background: transparent;
    justify-content: center;
    margin-top: 40px;

    @include width-1200 {
      margin-top: 16px;
    }
  }
  & .tabs__nav-item {
    max-width: 222px;

    @include width-1400 {
      max-width: 195px;
    }
    @include width-1200 {
      max-width: 170px;
    }
    @include width-768 {
      max-width: 100%;
    }
  }
  & .tabs__content {
    height: 684px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    position: relative;
    cursor: pointer;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(6, 55, 58, 0.5), rgba(6, 55, 58, 0.5)), url(image.png);
      mix-blend-mode: darken;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s;
    }
    &::before {
      content: '';
      position: absolute;
      width: 70px;
      height: 70px;
      background: url('../img/icons/scale.svg') no-repeat;
      z-index: 5;
      animation: infScale;
      animation-duration: 1s;
      animation-iteration-count: infinite;
      animation-direction: alternate;
      opacity: 0;
      visibility: hidden;
      transition: all 0.5s;
    }
    &:hover {
      &::after {
        opacity: 1;
        visibility: visible;
      }
      &::before {
        opacity: 1;
        visibility: visible;
      }
    }

    @include width-1400 {
      height: 544px;
    }
    @include width-1200 {
      height: 340px;
    }
  }
  & .tabs__panel {
    & img {
      mix-blend-mode: darken;
    }
    @include width-1200 {
      max-width: 340px;
    }
  }
  & .tabs__nav-btn {
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    padding: 15px 0;
    & svg {
      width: 22px;
      height: 22px;
      margin-right: 10px;
      transition: all 0.4s;
    }
    &:hover {
      & svg {
        fill: #159947;
      }
    }

    @include width-1400 {
      font-size: 18px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
      padding: 12px 0;
      & svg {
        width: 18px;
        height: 18px;
      }
    }
  }
  & .tabs .tabs__nav-btn--active {
    & svg {
      fill: #159947;
    }
  }
}

.product-info {
}

.p-info {

  &__body {
    background: #ffffff;
    padding: 60px 120px;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: 'first' 'table' 'last';

    @include width-1400 {
      padding: 50px 70px;
    }
    @include width-1200 {
      padding: 16px 16px 40px;
      grid-template-columns: 1fr 1fr;
      column-gap: 50px;
      grid-template-areas: 'first table' 'last table';
    }
    @include width-768 {
      grid-template-columns: 1fr;
      grid-template-areas: 'first' 'table' 'last';
    }
  }
  &__first {
    grid-area: first;
    height: fit-content;
  }
  &__last {
    grid-area: last;
    display: flex;
    flex-direction: column;
  }

  &__title {
    color: #159947;
    font-weight: 400;
  }

  &__params {
    display: flex;
    margin-top: 24px;
    & >:not(:last-child) {
      margin-right: 16px;
    }
  }

  &__param {
    background: rgba(21, 153, 71, 0.15);
    font-weight: 300;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.03em;
    color: #159947;
    padding: 8px 16px;

    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }

  &__table {
    margin-top: 24px;
    grid-area: table;
    & >:not(:last-child) {
      border-bottom: 1px solid #B4D0CE;
    }

    @include width-1200 {
      margin-top: 0;
    }
    @include width-768 {
      margin-top: 24px;
    }
  }

  &__line {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 0;
    & :first-child {
      font-weight: 300;
      font-size: 20px;
      line-height: 130%;
      font-feature-settings: 'pnum' on, 'lnum' on;
      color: #989898;

      @include width-1400 {
        font-size: 18px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
    & :last-child {
      font-weight: 400;
      font-size: 24px;
      line-height: 130%;
      font-feature-settings: 'pnum' on, 'lnum' on;
      color: #061A23;

      @include width-1400 {
        font-size: 20px;
      }
      @include width-1200 {
        font-size: 18px;
      }
      @include width-768 {
        font-size: 16px;
      }
    }
    &-amount {
      & :first-child {
        color: #061A23;
      }
    }
    &-big {
      padding: 16px 0;
      & :first-child {
        font-size: 24px;

        @include width-1400 {
          font-size: 20px;
        }
        @include width-1200 {
          font-size: 18px;
        }
        @include width-768 {
          font-size: 16px;
        }
      }
    }
    &-acc {
      position: relative;
      padding-right: 40px;
      cursor: pointer;
      &::after {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto 0;
        background: url('../img/icons/arrow_acc.svg') no-repeat;
        width: 17px;
        height: 10px;
        background-size: 17px;
        transition: all 0.4s;
      }
      &.active {
        &::after {
          transform: rotate(180deg);
        }
      }
    }
  }

  &__rooms {
    display: grid;
    grid-template-columns: 1fr 1fr;
    &-left {
      border-right: 1px solid #B4D0CE;
      padding-right: 16px;
    }
    &-right {
      padding-left: 16px;
    }
  }

  &__finish {
  }

  &__finish-more {
    font-weight: 300;
    font-size: 16px;
    line-height: 19px;
    color: #061A23;
    max-height: 0;
    overflow: hidden;
    transition: max-height .3s ease-out;

    @include width-1400 {
      font-size: 14px;
    }
    @include width-1200 {
      font-size: 12px;
    }
  }

  &__price {
    color: #159947;
    font-weight: 400;
    margin-top: 24px;

    @include width-1200 {
      margin-top: 0;
    }
    @include width-768 {
      margin-top: 24px;
    }
  }

  &__subprice {
    display: block;
    font-weight: 300;
    font-size: 24px;
    line-height: 29px;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #989898;
    margin-top: 8px;

    @include width-1400 {
      font-size: 20px;
    }
    @include width-1200 {
      font-size: 18px;
    }
    @include width-768 {
      font-size: 16px;
      margin-top: 0;
    }
  }

  &__btn {
    margin-top: 40px;
    max-width: 100%;

    @include width-1200 {
      margin-top: auto;
    }
    @include width-768 {
      margin-top: 40px;
    }
  }

  &__share {
    display: flex;
    align-items: center;
    margin-top: 32px;

    @include width-768 {
      width: fit-content;
      margin: 0 auto;
      margin-top: 32px;
    }
    & span {
      font-weight: 300;
      font-size: 20px;
      line-height: 24px;
      color: #000000;

      @include width-1400 {
        font-size: 18px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
  }

  &__share-items {
    margin-left: 24px;
    display: flex;
    align-items: center;
    & >:not(:last-child) {
      margin-right: 16px;

      @include width-1200 {
        margin-right: 24px;
      }
    }
    & a {
      transition: all 0.3s;
      &:hover {
        scale: 1.4;
        transition: all 0.3s;
      }
    }
  }
}

.product-other {
  margin-top: 80px;

  @include width-1400 {
    margin-top: 70px;
  }
  @include width-1200 {
    margin-top: 60px;
  }
  &__title {
    text-align: center;
  }
  &__cards {
    max-width: 1042px;
    margin: 0 auto;
    margin-top: 50px;

    @include width-1400 {
      margin-top: 44px;
      max-width: 774px;
    }
    @include width-1200 {
      margin-top: 40px;
    }
  }
}

// -------------/PRODUCT-------------
