.text {
  margin-bottom: 80px;
  @include width-1400 {
    margin-bottom: 70px;
  }
  @include width-1200 {
    margin-bottom: 60px;
  }
  &__container {
    max-width: 920px;
  }

  &__body {
    & h1 {
      font-weight: 275;
      font-size: 70px;
      line-height: 100%;
      color: #061A23;
      margin-bottom: 40px;
      @include width-1400 {
        font-size: 60px;
      }
      @include width-1200 {
        font-size: 40px;
      }
      @include width-768 {
        font-size: 30px;
      }
    }
    & h2 {
      font-weight: 300;
      font-size: 45px;
      line-height: 115%;
      color: #061A23;
      margin-bottom: 24px;
      @include width-1400 {
        font-size: 36px;
      }
      @include width-1200 {
        font-size: 28px;
      }
      @include width-768 {
        font-size: 24px;
      }
    }
    & h3 {
      font-weight: 500;
      font-size: 24px;
      line-height: 135%;
      color: #061A23;
      margin-bottom: 24px;
      @include width-1400 {
        font-size: 22px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
    & span {
      display: block;
      font-weight: 300;
      font-size: 24px;
      line-height: 135%;
      color: #061A23;
      margin-bottom: 24px;
      @include width-1400 {
        font-size: 22px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
      & p {
        &:not(:last-child) {
          margin-bottom: 16px;
        }
      }
    }
    & ul {
      padding-left: 16px;
      & li {
        font-weight: 300;
        font-size: 24px;
        line-height: 135%;
        color: #061A23;
        padding-left: 16px;
        position: relative;
        &::before {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          bottom: 0;
          margin: auto 0;
          background: #061A23;
          border-radius: 50%;
          width: 4px;
          height: 4px;
        }

        @include width-1400 {
          font-size: 22px;
        }
        @include width-1200 {
          font-size: 16px;
        }
        @include width-768 {
          font-size: 14px;
        }
      }
    }
  }
}

.headline {
  &__content {
    display: grid;
    grid-template-columns: 440px 1fr;
    gap: 32px;
    @include width-1400 {
      grid-template-columns: 310px 1fr;
    }
    @include width-1200 {
      grid-template-columns: 1fr;
      gap: 40px;
    }
  }
  &__img {
    @include width-1200 {
      max-width: 336px;
    }
  }
  &__body {
    margin-top: 80px;

    @include width-1400 {
      margin-top: 20px;
    }
    @include width-1200 {
      margin-top: 0;
    }
    & h5 {
      font-weight: 300;
      font-size: 24px;
      line-height: 100%;
      font-feature-settings: 'pnum' on, 'lnum' on;
      color: #061A23;
      margin-bottom: 20px;
      @include width-1400 {
        font-size: 20px;
      }
      @include width-1200 {
        font-size: 18px;
      }
      @include width-768 {
        font-size: 16px;
      }
    }
  }
}
