.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #ffffff;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  &.active {
    opacity: 1;
    visibility: visible;
    transition: 0.3s;
  }
  &-close {
    position: absolute;
    right: 50px;
    top: 40px;
    & img {
      width: 60px;
      height: 60px;
    }
  }
}

.menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #ffffff;
  transform: translateY(-100%);
  transition: 0.5s;
  overflow-y: scroll;
  &.menu--active {
    transform: translateY(0);
  }

  & .footer__slink {
    @include hover-link;
  }
  & .footer__slinks {
    margin-right: 50px;

    @include width-1200 {
      margin-left: 150px;
      grid-template-columns: 1fr;
    }
    @include width-768 {
      margin-left: 0;
      margin-right: 0;
      margin-top: 32px;
    }
  }
  @include width-1200 {
    & .footer__left {
      justify-content: flex-start;
    }
  }

  &__container {
    padding: 0 80px;
    @include width-1200 {
      padding: 0 40px;
    }
    @include width-768 {
      padding: 0 20px;
    }
  }
  &__close {
    background: none;
    border: none;
    padding: 0;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #222125;
    position: absolute;
    top: 40px;
    display: flex;
    align-items: center;
    cursor: pointer;
    z-index: 10;
    transition: all 0.3s;
    & svg {
      stroke: #061A23;
      stroke-width: 2px;
      width: 40px;
      height: 40px;
      margin-right: 15px;
      transition: all 0.3s;
    }
    &:hover {
      color: #159947;
      & svg {
        stroke: #159947;
      }
    }

    @include width-1400 {
      font-size: 18px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      top: 55px;
      font-size: 14px;

      & svg {
        margin-right: 10px;
      }
    }
  }

  &__content {
    padding: 160px 0;

    @include width-1400 {
      padding: 110px 0;
    }
    @include width-1200 {
      padding: 40px 0;
    }
    @include width-768 {
      padding: 20px 0 60px;
    }
  }

  &__hat {
    display: grid;
    grid-template-areas: 'logo info'
                         'links info';
    justify-content: space-between;
    gap: 40px;

    @include width-1200 {
      grid-template-areas: 'logo'
                           'links'
                           'info';
      grid-template-columns: 1fr;
      gap: 0;
    }
  }

  &__logo {
    grid-area: logo;
    margin-bottom: 50px;
    max-width: 460px;

    @include width-1200 {
      justify-self: flex-end;
      max-width: 380px;
    }
    @include width-768 {
      justify-self: center;
      max-width: 230px;
      margin-bottom: 80px;
    }
  }
  &__links {
    grid-area: links;
    width: 560px;

    @include width-1200 {
      width: 100%;
    }
  }
  &__info {
    grid-area: info;
    & .footer-item__title {
      color: #989898;
    }

    @include width-1200 {
      margin-top: 80px;
    }
    @include width-768 {
      margin-top: 40px;
    }
  }

  &__footer {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    @include width-1200 {
      margin-top: 100px;
    }
    @include width-768 {
      flex-direction: column;
      align-items: center;
      margin-top: 35px;
    }
  }

  &__btn {
    flex-shrink: 0;
    & svg {
      margin-left: 15px;
      width: 20px;
      height: 20px;
    }
  }

  &__request {
    width: 100%;
    max-width: 370px;
    & span {
      font-weight: 300;
      font-size: 20px;
      line-height: 130%;
      color: #061A23;
    }
    & button {
      margin-top: 16px;
      max-width: 100%;
    }

    @include width-1200 {
      margin-left: 135px;
    }
    @include width-768 {
      margin-left: 0;
      max-width: 100%;
      margin-top: 32px;
    }
  }
}

.graph-modal {
  background-color: rgb(6, 55, 58, 0.5);
  backdrop-filter: blur(3px);
  &__container {
    border-radius: 0;
  }
  &__close {
    background-image: url('../img/icons/close_popup.svg');
    width: 40px;
    height: 40px;
    @include width-1200 {
      top: -48px;
      right: 0;
      width: 40px;
      height: 40px;
    }
  }
}

.popup-flat {
  & .filter__btn {
    & svg {
      stroke-width: 0;
      width: 20px;
      height: 20px;
    }
  }
  & .graph-modal__container {
    padding: 32px;
    max-width: 410px;

    @include width-768 {
      padding: 24px;
    }
  }
}

.popup-citybox {
  &__body {
    display: grid;
    grid-template-columns: 1fr 1fr;

    @include width-1200 {
      grid-template-columns: 1fr;
    }
  }
  &__left {
    padding: 40px;

    @include width-1200 {
      display: flex;
      justify-content: space-between;
      padding: 32px;
    }
    @include width-768 {
      flex-direction: column;
      padding: 24px;
    }
  }
  &__params {
    display: flex;
    margin-top: 16px;
    & span {
      display: block;
      font-weight: 300;
      font-size: 20px;
      line-height: 24px;
      color: #061A23;
      &:last-child {
        margin-left: 50px;
      }

      @include width-1400 {
        font-size: 18px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
  }
  &__price {
    display: block;
    font-weight: 400;
    font-size: 28px;
    line-height: 125%;
    color: #061A23;
    margin-top: 32px;

    @include width-1400 {
      font-size: 26px;
      margin-top: 50px;
    }
    @include width-1200 {
      font-size: 24px;
      margin-top: 40px;
    }
    @include width-768 {
      font-size: 20px;
      margin-top: 32px;
    }
  }

  &__right {
    background: #1F5F5B;
    padding: 40px;

    & h4 {
      color: #FFFFFF;
    }
    &-text {
      @include width-1200 {
        max-width: 260px;
      }
    }

    @include width-1200 {
      display: flex;
      padding: 32px;
      & >:last-child {
        margin-left: 40px;
      }
    }
    @include width-768 {
      flex-direction: column;
      padding: 24px;
    }
  }

  &__img {
    width: 100%;
    display: block;
    margin-top: 80px;

    @include width-1200 {
      max-width: 320px;
      align-self: center;
      margin-top: 0;
    }
    @include width-768 {
      margin-top: 32px;
      max-width: 230px;
    }
  }

  & .graph-modal__container {
    padding: 0;
    max-width: 1000px;
  }

  & .req__form {
    margin-top: 32px;
    margin-bottom: 0;

    @include width-1200 {
      max-width: 100%;
      margin-top: 0;
    }
    @include width-768 {
      margin-top: 32px;
      margin-left: 0;
      padding: 0;
    }
  }
}

.popup-req {
  & .graph-modal__container {
    max-width: 610px;
    padding: 0;
  }
  & .req__form {
    max-width: 100%;
    & input {
      color: #061A23;
      border-bottom: 2px solid #061A23;
    }
    & .input__placeholder {
      color: #061A23;
      &.focused {
        color: #989898;
      }
    }
    & .input:not(:first-child) {
      margin-top: 24px;
    }
    & .btn-green {
      max-width: 100%;
      margin-top: 40px;
    }
  }
  & .req__agree:first-child {
    margin-top: 0;
    color: #061A23;
    font-size: 24px;
    line-height: 135%;
    margin-bottom: 24px;

    @include width-1400 {
      font-size: 22px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }
  & .req__agree:last-child {
    color: #989898;
    & a {
      &:hover {
        color: #061A23;
      }
    }
  }
  &__body {
    padding: 24px 40px 40px;

    @include width-768 {
      padding: 24px 20px 20px;
    }
  }

  &__hat {
    background: #1F5F5B;
    padding: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    & svg {
      width: 100px;
      height: 100px;

      @include width-1400 {
        width: 90px;
        height: 90px;
      }
      @include width-1200 {
        width: 75px;
        height: 75px;
      }
      @include width-768 {
        width: 60px;
        height: 60px;
      }
    }
    @include width-768 {
      padding: 20px;
    }
  }

  &__title {
    text-align: center;
    color: #FFFFFF;
  }

  &__subtitle {
    font-weight: 300;
    font-size: 28px;
    line-height: 125%;
    text-align: center;
    color: #FFFFFF;

    @include width-1400 {
      font-size: 26px;
    }
    @include width-1200 {
      font-size: 24px;
    }
    @include width-768 {
      font-size: 20px;
    }
  }
}

.popup-success {

  &__body {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    & .req__agree:last-child {
      color: #061A23;
      margin-top: 24px;
    }
  }
}

.p-fancy {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  background: #ffffff;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
  pointer-events: none;
  overflow-y: auto;
  &.active {
    opacity: 1;
    visibility: visible;
    transition: 0.3s;
    pointer-events: all;
  }
  & .graph-modal__close {
    background: url('../img/icons/close_black.svg');
    top: 0;
    right: 80px;

    @include width-1200 {
      right: 40px;
      top: -64px;
    }
    @include width-768 {
      right: 20px;
    }
  }

  &__body {
    padding: 100px 0;

    @include width-1400 {
      padding: 80px 0;
    }
    @include width-1200 {
      padding: 130px 0;
    }
    @include width-768 {
      padding: 100px 0;
    }
  }

  &__container {
    display: flex;
    justify-content: center;
    position: relative;

    @include width-1200 {
      justify-content: space-between;
    }
    @include width-768 {
      flex-direction: column;
    }
  }

  &__prevs {
    position: absolute;
    top: 0;
    left: 80px;

    @include width-1200 {
      left: 40px;
      position: static;
    }
    @include width-768 {
      left: 20px;
      order: 1;
      display: flex;
      margin-top: 16px;
    }
  }

  &__prev {
    width: 100px;
    height: 100px;
    border: 1px solid #1F5F5B;
    position: relative;
    transition: all 0.3s;
    cursor: pointer;
    &:not(:last-child) {
      margin-bottom: 24px;
      @include width-768 {
        margin-bottom: 0;
        margin-right: 24px;
      }
    }
    &:hover {
      border: 1px solid transparent;
      transition: all 0.3s;
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: linear-gradient(0deg, rgba(6, 55, 58, 0.5), rgba(6, 55, 58, 0.5));
        mix-blend-mode: darken;
        transition: all 0.5s;
      }
    }
    &.active {
      border: 1px solid transparent;
      transition: all 0.3s;
      &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: linear-gradient(0deg, rgba(6, 55, 58, 0.5), rgba(6, 55, 58, 0.5));
        mix-blend-mode: darken;
        transition: all 0.5s;
      }
    }

    @include width-1400 {
      width: 80px;
      height: 80px;
    }
  }

  &__pics {
    max-width: 800px;

    @include width-1400 {
      max-width: 544px;
    }
    @include width-1200 {
      max-width: 510px;
      margin-right: 5%;
    }
    @include width-768 {
      max-width: 100%;
      margin-right: 0;
      order: 0;
    }
  }

  &__pic {
  }
}
