.contacts {
  margin-top: 50px;

  @include width-1200 {
    margin-top: 40px;
  }
  & .footer-item__subtitle {
    font-size: 24px;
    @include width-1400 {
      font-size: 20px;
    }
    @include width-1200 {
      font-size: 18px;
    }
    @include width-768 {
      font-size: 16px;
    }
  }
  & .footer-item__title {
    @include width-1400 {
      font-size: 18px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }
  & .menu__info {
    margin-right: 50px;
    @include width-1200 {
      margin: 0;
    }
  }
  &__body {
    display: flex;
    justify-content: space-between;

    @include width-1200 {
      flex-direction: column;
    }
  }
  &__map {
    height: 600px;
    width: 100%;
    max-width: 923px;
    filter: drop-shadow(5px 6px 14px rgba(0, 0, 0, 0.25));

    @include width-1400 {
      max-width: 660px;
    }
    @include width-1200 {
      height: 480px;
      max-width: 100%;
      margin-top: 40px;
    }
    @include width-768 {
      height: 264px;
    }
  }
}
