.citybox {
  &__chooses {
    display: flex;
    align-self: center;

    @include width-768 {
      width: 100%;
    }
  }
  &__choose {
    background: none;
    padding: 15px 0;
    border: none;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #061A23;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: 1px solid transparent;
    transition: all 0.4s;
    width: 222px;
    margin-top: 50px;

    &:hover {
      border: 1px solid #159947;
      color: #159947;
    }
    &.active {
      border: 1px solid #159947;
      color: #159947;
    }

    @include width-1400 {
      font-size: 18px;
      width: 170px;
    }
    @include width-1200 {
      font-size: 16px;
      margin-top: 40px;
    }
    @include width-768 {
      font-size: 14px;
      padding: 10px 0;
      width: 100%;
    }
  }

  &__body {
    margin-top: 50px;
  }

  &-scheme {
    height: 920px;

    @include width-1400 {
      height: 55vw;
    }
    @include width-1200 {
      height: 80vw;

    }
    @include width-768 {
      height: 600px;
      width: auto;
      margin-left: -20px;
      margin-right: -20px;
    }
  }
}
