.safe {
  margin-top: 50px;

  @include width-1200 {
    margin-top: 40px;
  }
  &__subtitle {
    display: block;
    font-weight: 300;
    font-size: 28px;
    line-height: 125%;
    color: #1F5F5B;
    @include width-1400 {
      font-size: 26px;
    }
    @include width-1200 {
      font-size: 24px;
    }
    @include width-768 {
      font-size: 20px;
      margin-top: 8px;
    }
  }
  &__body {
  }

  &__line {
    display: grid;
    align-items: center;
    grid-template-columns: 1fr 1fr;
    gap: 32px;

    @include width-768 {
      grid-template-columns: 1fr;
      gap: 24px;
    }

    &-first {
      & .safe__text span {
        max-width: 425px;
      }
    }
    &-second {
      margin-top: 32px;

      @include width-1400 {
        margin-top: 50px;
      }
      @include width-1200 {
        margin-top: 40px;
        align-items: flex-start;
      }
      @include width-768 {
        margin-top: 24px;
      }
    }
  }

  &__text {
    @include width-768 {
      order: 1;
    }
    & span {
      display: block;
      font-weight: 300;
      font-size: 24px;
      line-height: 135%;
      color: #061A23;
      margin-top: 32px;
      & p {
        &:not(:last-child) {
          margin-bottom: 20px;
          @include width-768 {
            margin-bottom: 8px;
          }
        }
      }

      @include width-1400 {
        font-size: 22px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
        margin-top: 16px;
      }
    }
  }

  &__img {
    @include width-768 {
      order: 0;
    }
  }

  &__footer {
    margin-top: 180px;
    margin-bottom: 180px;
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-between;
    align-items: flex-end;
    gap: 100px;

    @include width-1400 {
      margin-top: 150px;
      margin-bottom: 150px;
    }
    @include width-1200 {
      margin-top: 120px;
      margin-bottom: 120px;
      grid-template-columns: 1fr;
      gap: 40px;
    }
    @include width-768 {
      gap: 24px;
      margin-top: 85px;
      margin-bottom: 85px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    max-width: 720px;
    & a {
      font-weight: 400;
      font-size: 45px;
      line-height: 115%;
      color: #061A23;
      margin-top: 32px;

      @include width-1400 {
        font-size: 36px;
      }
      @include width-1200 {
        font-size: 28px;
        margin-top: 24px;
      }
      @include width-768 {
        font-size: 24px;
        margin-top: 16px;
      }
    }
  }
}
