.house {

  &__hat {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include width-1200 {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__links {
    display: flex;
    flex-direction: column;

    @include width-1200 {
      width: 100%;
      flex-direction: row;
      justify-content: space-between;
      margin-top: 30px;
    }
    @include width-768 {
      flex-direction: column;
      margin-top: 70px;
    }
    & a {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
      & svg {
        width: 17px;
        height: 17px;
        margin-left: 20px;
      }
    }
  }

  &__title {
    text-align: center;
  }

  &__img {
    margin-top: 50px;
    position: relative;

    @include width-1200 {
      margin-top: 300px;
    }
    @include width-768 {
      margin-top: 24px;
      margin-bottom: 200px;
    }
    & svg {
      width: 100%;
      height: auto;
      position: relative;
      z-index: 2;
    }
    &-body {
      width: 100%;
      position: absolute;
      z-index: 1;
    }
    &-house {
      fill-opacity: 0;
      stroke-width: 0;
      cursor: pointer;
      transition: all 0.1s;
      &.active {
        fill-opacity: 0.2;
        stroke-width: 15;
      }
    }
  }

  &__params {
    margin-top: 24px;

    @include width-1400 {
      margin-top: 20px;
    }
    @include width-1200 {
      margin-top: 14px;
    }
  }

  &__chooses {
    display: none;
    margin-top: 24px;
    & >:first-child {
      margin-right: 80px;
    }

    @include width-768 {
      display: flex;
      align-self: center;
    }
  }
  &__choose {
    font-weight: 300;
    font-size: 20px;
    line-height: 125%;
    color: #159947;
    position: relative;
    padding-left: 46px;
    cursor: pointer;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      margin: auto 0;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      border: 1px solid #159947;
    }
    &::before {
      content: '';
      position: absolute;
      left: 7px;
      top: 0;
      bottom: 0;
      margin: auto 0;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      background: #159947;
      opacity: 0;
      visibility: hidden;
    }
    &.active {
      &::before {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

.house-hover {
  box-sizing: border-box;
  width: 100%;
  background: #ffffff;
  max-width: 282px;
  padding: 30px 24px;
  position: relative;
  box-shadow: 5px 6px 14px rgba(0, 0, 0, 0.25);
  position: absolute;
  z-index: 4;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s;
  &.active {
    opacity: 1;
    visibility: visible;
  }

  @include width-1400 {
    max-width: 260px;
  }
  @include width-1200 {
    padding: 20px;
    opacity: 1;
    visibility: visible;
  }
  @include width-768 {
    padding: 20px 16px;
    max-width: 210px;
    opacity: 0;
    visibility: hidden;
  }
  & h4 {
    text-align: center;
    @include width-768 {
      display: none;
    }
  }
  & span {
    display: block;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    color: #000000;
    margin-top: 8px;

    @include width-1400 {
      font-size: 18px;
      margin-top: 4px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }

  &__first {
    top: 150px;
    left: 33%;

    @include width-1400 {
      left: 35%;
    }
    @include width-1200 {
      top: -250px;
      left: 15%;
    }
    @include width-960 {
      left: 8%;
    }
    @include width-960 {
      left: 8%;
    }
    @include width-768 {
      top: auto;
      bottom: -200px;
    }
    @include width-420 {
      left: 3%;
    }
    &::before {
      content: '';
      position: absolute;
      left: -28px;
      top: 30px;
      width: 0;
      height: 0;
      border-top: 18px solid transparent;
      border-bottom: 18px solid transparent;

      border-right: 30px solid #ffffff;

      @include width-1200 {
        left: 50%;
        transform: translateX(-50%);
        top: auto;
        bottom: -45px;
        border-top: 30px solid #ffffff;
        border-left: 18px solid transparent;
        border-right: 18px solid transparent;
      }
      @include width-768 {
        border-top: none;
        border-bottom: 30px solid #ffffff;
        border-left: 18px solid transparent;
        border-right: 18px solid transparent;
        bottom: auto;
        top: -28px;
      }
      @include width-420 {
        left: 30%;
      }
    }
  }

  &__sec {
    top: 300px;
    left: 37%;

    @include width-1400 {
      top: 250px;
      left: 32%;
    }
    @include width-1200 {
      top: -250px;
      left: auto;
      right: 15%;
    }
    @include width-960 {
      right: 8%;
    }
    @include width-768 {
      top: auto;
      bottom: -200px;
    }
    &::before {
      content: '';
      position: absolute;
      right: -28px;
      top: 30px;
      width: 0;
      height: 0;
      border-top: 18px solid transparent;
      border-bottom: 18px solid transparent;

      border-left: 30px solid #ffffff;

      @include width-1200 {
        left: 50%;
        transform: translateX(-50%);
        top: auto;
        bottom: -45px;
        border-top: 30px solid #ffffff;
        border-left: 18px solid transparent;
        border-right: 18px solid transparent;
      }
      @include width-768 {
        border-top: none;
        border-bottom: 30px solid #ffffff;
        border-left: 18px solid transparent;
        border-right: 18px solid transparent;
        bottom: auto;
        top: -28px;
      }
      @include width-420 {
        left: 60%;
      }
    }
  }
  &__btn {
    margin-top: 24px;

    @include width-1200 {
      margin-top: 16px;
    }
  }
}

.house-param {
  margin-top: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  & img {
    width: 45px;
    height: 45px;

    @include width-1400 {
      width: 40px;
      height: 40px;
    }
    @include width-768 {
      width: 35px;
      height: 35px;
    }
  }
  & h5 {
    font-weight: 400;
    font-size: 24px;
    line-height: 29px;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #159947;

    @include width-1400 {
      font-size: 20px;
    }
    @include width-1200 {
      font-size: 18px;
    }
    @include width-768 {
      font-size: 16px;
    }
  }
  & span {
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
    margin-top: 0;

    @include width-1400 {
      font-size: 18px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }
}
