// ---------------FLAT-FILTER-----------------
.flat {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 320px 1fr;
  gap: 32px;

  @include width-1400 {
    grid-template-columns: 300px 1fr;
  }
  @include width-1200 {
    grid-template-columns: 1fr;
    margin-top: 40px;
  }
  &__title {
    text-align: center;
    margin: 0 auto;

    @include width-768 {
      margin: 0;
    }
  }
  &__item {
    display: none;
    position: relative;
    &.active {
      display: block;
    }
  }
}

.filter {
  height: fit-content;
  max-width: 320px;
  background: #ffffff;
  padding: 25px;
  box-sizing: border-box;
  margin-top: 80px;

  @include width-1200 {
    display: none;
  }
  &__title {
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #1F5F5B;
    margin-bottom: 24px;

    @include width-1200 {
      font-size: 16px;
    }
  }

  &__rooms {
    display: flex;
    justify-content: space-between;

    @include width-1200 {
      justify-content: flex-start;

      & >:not(:last-child) {
        margin-right: 37px;
      }
    }
    @include width-768 {
      & >:not(:last-child) {
        margin-right: 26px;
      }
    }
  }

  &__room {
    cursor: pointer;
    & path {
      fill: #159947;
      transition: all .3s;
    }
    & rect {
      transition: all .3s;
    }
    &:hover {
      & path {
        fill: #ffffff;
      }
      & rect {
        fill: #159947;
        stroke: #159947;
      }
    }
    &.active {
      & path {
        fill: #ffffff;
      }
      & rect {
        fill: #1F5F5B;
      }
    }

    @include width-768 {
      width: 56px;
      height: 56px;
    }
  }

  &__btn {
    margin: 0 auto;
    margin-top: 80px;

    @include width-1200 {
      margin-top: 60px;

    }
    @include width-768 {
      margin-top: 50px;
    }
    & svg {
      width: 30px;
      height: 30px;
      margin-left: 20px;
      stroke: #159947;
      stroke-width: 2px;
    }
    &:hover {
      & svg {
        stroke: #06373A;
      }
    }
  }
}
.filter-range {
  margin-top: 50px;
  &__amounts {
    display: flex;
    justify-content: space-between;
    margin-top: 10px;
  }

  &__amount {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #061A23;
    & input {
      font-weight: 400;
      font-size: 20px;
      line-height: 24px;
      color: #061A23;
      padding: 0;
      background: none;
      border: none;
      &::placeholder {
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        color: #061A23;
      }
    }
  }
}
.filter-range-body {
}

.noUi-horizontal {
  height: 4px;
  border-radius: 0;
  background: #159947;
  border: none;
  box-shadow: none;
}

.noUi-connect {
  background: #06373A;
}

.noUi-handle {
  background: #06373A;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: none;
  box-shadow: none;
}

.noUi-horizontal .noUi-handle {
  width: 18px;
  height: 18px;
  // right: 0;
  // left: -17px;
  // left: 0;
}

.noUi-handle:before, .noUi-handle:after {
  display: none;
}

.noUi-horizontal .noUi-handle-upper {
  right: -1px;
}

.noUi-active {
  background: #159947;
  border: 4px solid #06373A;
}

// ---------------/FLAT-FILTER-----------------

// ---------------FLAT-CONTENT-----------------
.flat {
  &__body {
  }

  &__cards {
    width: 100%;

    &-more {
      margin: 0 auto;
      margin-top: 35px;
      & svg {
        width: 20px;
        height: 20px;
        margin-left: 20px;
      }
    }
  }
  &__content {
    margin-top: 24px;
  }
}

.flat-hat {
  display: flex;
  justify-content: space-between;

  @include width-1200 {
    flex-direction: column;
    align-items: center;
  }
  &__sort {
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #061A23;
    display: flex;
    align-items: center;

    @include width-1400 {
      font-size: 18px;
    }
    @include width-1200 {
      order: 2;
      align-self: flex-start;
      margin-top: 24px;
      font-size: 16px;
    }
    @include width-768 {
      align-self: center;
      font-size: 14px;
    }
  }

  &__select {
    margin-left: 20px;
    @include width-1400 {
      margin-left: 5px;
    }
    @include width-768 {
      margin-left: 0;
    }
  }

  &__filter {
    display: none;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    order: 1;
    margin-top: 40px;

    @include width-1200 {
      display: flex;
    }
    @include width-768 {
      flex-direction: column;
    }
    &-btn {
      box-sizing: border-box;
      padding: 12px 32px;
      max-width: 160px;
      background: #FFFFFF;
      border: none;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 0.06em;
      text-transform: uppercase;
      color: #1F5F5B;
      & svg {
        width: 20px;
        height: 20px;
        stroke: #1F5F5B;
        fill: none;
        margin-right: 10px;
      }

      margin-right: 16px;

      @include width-768 {
        width: 100%;
        max-width: 100%;
      }
    }
    &-left {
      display: flex;

      @include width-768 {
        flex-direction: column;
        width: 100%;
      }
    }
    &-items {
      display: flex;
      & >:not(:last-child) {
        margin-right: 16px;

        @include width-768 {
          margin-right: 10px;
        }
      }

      @include width-768 {
        margin-top: 10px;
        flex-wrap: wrap;
      }
    }
    &-item {
      font-weight: 400;
      font-size: 16px;
      line-height: 19px;
      color: #1F5F5B;
      padding: 12px;
      border: 1px solid #1F5F5B;
      white-space: nowrap;

      @include width-768 {
        margin-top: 10px;
      }
    }
    &-clear {
      color: #1F5F5B;
      justify-self: flex-end;
      & svg {
        width: 20px;
        height: 20px;
        stroke: #1F5F5B;
        stroke-width: 3px;
        margin-left: 10px;
      }

      @include width-768 {
        margin-top: 20px;
        align-self: flex-start;
      }
    }
  }
}

.flat-tabs {
  display: flex;
  margin-left: auto;

  @include width-1200 {
    margin-left: 0;
  }
  @include width-768 {
    width: 100%;
  }
  &__btn {
    background: none;
    padding: 15px 0;
    border: none;
    font-weight: 300;
    font-size: 20px;
    line-height: 24px;
    color: #061A23;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border: 1px solid transparent;
    transition: all 0.4s;
    width: 222px;
    & svg {
      width: 22px;
      height: 22px;
      margin-right: 10px;
      transition: all 0.4s;
    }

    &:hover {
      border: 1px solid #159947;
      color: #159947;
      & svg {
        fill: #159947;
      }
    }
    &.active {
      border: 1px solid #159947;
      color: #159947;
      & svg {
        fill: #159947;
      }
    }

    @include width-1400 {
      font-size: 18px;
      width: 170px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
      padding: 10px 0;
      width: 100%;
    }
  }
}

.flat-card {
  // width: 100%;
  max-width: 100%;
  display: grid;
  grid-template-columns: repeat(3,auto);
  justify-content: space-between;
  background: #ffffff;
  padding: 24px;
  border: 1px solid #B4D0CE;
  transition: all 0.3s;
  gap: 20px;
  &:hover {
    border: 1px solid #1F5F5B;
    box-shadow: inset 0px 0px 0px 1px #1F5F5B;
  }

  @include width-1400 {
    padding: 24px 20px;
    gap: 10px;
  }
  @include width-768 {
    grid-template-columns: 1fr;
    padding: 20px;
  }
  &__left {
    height: 100%;
    display: flex;
    flex-direction: column;
    & h4 {
      color: #159947;
    }
  }

  &__left-items {
    display: flex;
    margin-top: auto;
    & >:first-child {
      margin-right: 8px;

      @include width-768 {
        margin-right: 20px;
      }
    }

    @include width-768 {
      margin-top: 20px;
    }
  }

  &__info {
    display: flex;

    @include width-768 {
      justify-content: space-between;
    }
  }

  &__img {
    max-width: 152px;
    margin-left: 32px;
    align-self: center;

    @include width-1400 {
      max-width: 140px;
      margin-left: 16px;
    }
    @include width-1200 {
      margin-left: 40px;
      max-width: 130px;
    }
    @include width-960 {
      margin-left: 20px;
    }
    @include width-768 {
      max-width: 140px;
    }
  }

  &__stats {
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 48px;
    align-self: center;

    @include width-1700 {
      gap: 20px;
    }
    @include width-1400 {
      gap: 10px;
    }
    @include width-1200 {
      gap: 40px;
    }
    @include width-960 {
      grid-template-columns: 1fr 1fr;
      gap: 0;
      row-gap: 20px;
      column-gap: 40px;
    }
    @include width-768 {
      grid-template-columns: repeat(4,auto);
      margin-top: 16px;
      column-gap: 20px;
    }
  }

  &__stat {
    display: flex;
    flex-direction: column;
    & h6 {
      font-weight: 300;
      font-size: 20px;
      line-height: 130%;
      color: #989898;

      @include width-1400 {
        font-size: 18px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
    & span {
      font-weight: 400;
      font-size: 20px;
      line-height: 130%;
      color: #061A23;

      @include width-1400 {
        font-size: 18px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
  }

  &__price {
    max-width: 160px;

    @include width-768 {
      max-width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-top: 20px;
    }
  }

  &__price-left {
    height: 100%;
    display: flex;
    flex-direction: column;
    @include width-768 {
      width: 100%;
      max-width: 140px;
      margin-right: 10px;
    }

    & h4 {
      white-space: nowrap;
    }
    & span {
      display: block;
      font-weight: 300;
      font-size: 20px;
      line-height: 24px;
      color: #989898;
      margin-bottom: 20px;

      @include width-1400 {
        font-size: 18px;
      }
      @include width-1200 {
        font-size: 16px;
      }
      @include width-768 {
        font-size: 14px;
      }
    }
  }

  &__btn {
    display: none;
    font-size: 14px;
    & svg {
      width: 17px;
      height: 17px;
      margin-left: 16px;
    }

    @include width-768 {
      display: flex;
    }
    @include width-420 {
      & svg {
        margin-left: 6px;
      }
    }
  }
}

.flat-icon {
  width: 64px;
  height: 64px;
  background: rgba(21, 153, 71, 0.15);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #159947;

  @include width-1400 {
    font-size: 16px;
    width: 56px;
    height: 56px;
  }
  &__rooms {
    font-weight: 300;
    & *:first-child {
      font-size: 40px;
      font-weight: 400;
      margin-right: 3px;
    }
  }
}

.flat-available {
  font-weight: 300;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.03em;
  padding: 8px;
  margin-top: auto;

  @include width-1400 {
    font-size: 16px;
  }
  @include width-1200 {
    padding: 5px;
  }
  &__onsale {
    color: #159947;
    background: rgba(21, 153, 71, 0.15);
  }
  &__booked {
    color: #FFBD70;
    background: rgba(255, 155, 112, 0.15);
  }
  &__sold {
    color: #FF473C;
    background: rgba(255, 71, 60, 0.15);
  }
}

.choices__inner {
  border-radius: 0;
  width: 220px;
  border: none;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  color: #159947;
  text-align: start;
  background-color: none;
  min-height: auto;
  padding: 0 20px;
  padding-right: 30px;

  @include width-1400 {
    font-size: 18px;
    width: 200px;
  }
  @include width-1200 {
    font-size: 16px;
    width: 180px;
  }
  @include width-768 {
    font-size: 14px;
    width: 160px;
    padding: 0 10px;
    padding-right: 30px;
  }
}

.is-focused .choices__inner, .is-open {
  border-color: none;
}

.is-open .choices-list--dropdown, .is-open .choices__list[aria-expanded] {
  border-color: none;
}

.choices__list--dropdown, .choices__list[aria-expanded] {
  border-radius: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 120%;
  filter: drop-shadow(5px 6px 14px rgba(0, 0, 0, 0.25));
}

.choices__list--dropdown, .choices__list[aria-expanded] {
  z-index: 3;
}

.archive__select .choices__inner {
  background: transparent;
}

.archive__select .is-open .choices__list--dropdown {
  background: #FFFFFF;
}

.work-yes__select .choices__inner {
  background: transparent;
}

.work-yes__select .is-open .choices__list--dropdown {
  background: #FFFFFF;
}

.is-open .choices__inner {
  border-radius: 0;
  background: transparent;
}

.is-open .choices__list--dropdown {
  border-radius: 0;
  font-weight: 300;
  font-size: 20px;
  line-height: 24px;
  color: #159947;
  border: none;

  @include width-1400 {
    font-size: 18px;
  }
  @include width-1200 {
    font-size: 16px;
  }
  @include width-768 {
    font-size: 14px;
  }
}

.choices__list--dropdown .choices__item--selectable.is-highlighted::after, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted::after {
  display: none;
}

.choices[data-type*=select-one]::after {
  border: none;
  width: 12px;
  height: 12px;
  right: 15px;
  background: url('../img/icons/arrow_select.svg') no-repeat;
  background-size: 12px;
  transition: 0.4s;
}

.choices[data-type*=select-one].is-open::after {
  border: none;
  width: 12px;
  height: 12px;
  transform: rotateX(180deg);
  transition: 0.4s;
}

.choices__placeholder {
  opacity: 0.5;
}

.choices__placeholder[data-choice-selectable] {
  display: none;
}

.choices__list--dropdown .choices__item, .choices__list[aria-expanded] .choices__item {
  font-size: 20px;
  // padding: 0;
  // padding-bottom: 10px;
  padding: 6px 20px;
  text-align: start;

  @include width-1400 {
    font-size: 18px;
  }
  @include width-1200 {
    font-size: 16px;
  }
  @include width-768 {
    font-size: 14px;
    padding: 6px 10px;
  }
}

.choices__list--dropdown .choices__item--selectable, .choices__list[aria-expanded] .choices__item--selectable {
  padding-right: 20px;
  @include width-768 {
    padding-right: 10px;
  }
}

.choices__list--dropdown .choices__item--selectable.is-highlighted, .choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  background: #1F5F5B;
  border-radius: 0;
  color: #ffffff;
}

.choices__list {
  padding: 0px;
  // padding-bottom: 0px;
}

.choices__list--single {
  // padding: 0px;
}

.choices[data-type*=select-one] .choices__inner {
  padding-bottom: 0;
}

.choices__list--dropdown .choices__item, .choices__list[aria-expanded] .choices__item {
  padding-bottom: 6px;
}
.choices__list--dropdown, .choices__list[aria-expanded] {
  margin-top: 8px;
}

// ---------------/FLAT-CONTENT-----------------


.flat-scheme {
  box-sizing: border-box;
  overflow: hidden;
  width: 100%;
  height: 880px;
  // border: 1px solid #663333;
  position: relative;
  cursor: grab;
  display: flex;
  align-items: center;
  justify-content: center;

  @include width-1400 {
    height: 55vw;
  }
  @include width-1200 {
    height: 80vw;
  }
  @include width-768 {
    height: 600px;
    width: auto;
    margin-left: -20px;
    margin-right: -20px;
  }
  &__compass {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    width: 80px;
    height: 80px;

    @include width-1400 {
      height: 60px;
      width: 60px;
    }
    @include width-1200 {
      height: 50px;
      width: 50px;
    }
    @include width-768 {
      left: 20px;
      top: 10px;
      height: 40px;
      width: 40px;
    }
  }

  &__overlay {
    position: absolute;
    z-index: 5;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(6, 55, 58, 0.8);
    backdrop-filter: blur(3px);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.4s;
    &.hidden {
      opacity: 0;
      visibility: hidden;
    }

    &-body {
      display: flex;
      align-items: center;
      & span {
        display: block;
        margin-left: 20px;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #FFFFFF;
      }
    }
  }

  &__zooms {
    position: absolute;
    top: 45%;
    left: 0;
    z-index: 4;
    display: flex;
    flex-direction: column;
    @include width-768 {
      display: none;
    }
  }
  &__zoom {
    // fill: transparent;
    // stroke: #159947;
    width: 40px;
    height: 40px;
    cursor: pointer;
    & rect {
      fill: #f9f9f9;
    }
    &:hover {
      & rect {
        fill: #159947;
        transition: all 0.3s;
      }
      & path {
        fill: #ffffff;
        transition: all 0.3s;
      }
    }
    &:active {
      & rect {
        fill: #1F5F5B;
        transition: all 0.3s;
      }
      & path {
        fill: #ffffff;
        transition: all 0.3s;
      }
    }
    &-in {

    }
    &-out {
      margin-top: 16px;
    }
  }

  &__img {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    height: auto;
    scale: 0.8;
    width: 100%;
    margin-top: -6%;
    // margin: 0 auto;
    transition: scale 0.2s;
    display: none;

    &.active {
      display: block;
    }

    & img {
      width: 100%;
    }
    & svg {
      width: 100%;
      height: auto;
      position: absolute;
      left: 0;
      top: 0;
      & path {
        cursor: pointer;
        z-index: 2;
        stroke-width: 6px;
        &:hover {
          stroke: #159947;
          stroke-width: 6px;
          fill: #159947;
          fill-opacity: 0.2;
        }
      }
    }

    @include width-768 {
      scale: 1;
      // width: 800px;
    }
    @include width-640 {
      scale: 1.2;
      width: 110%;
      margin-top: 10vw;
    }
    @include width-420 {
      width: 400px;
      margin-top: 28vw;
    }
    @include width-320 {
      margin-top: 0;
      margin-top: 40vw;
    }

    &-citybox {
      scale: 0.55;
      margin-top: -13%;

      @include width-1200 {
        scale: 0.8;
        margin-top: -5%;
      }
      @include width-768 {
        scale: 1;
      }
      @include width-640 {
        scale: 1.2;
        margin-top: 10%;
      }
      @include width-420 {
        scale: 1.3;
        margin-top: 38%;
      }
    }
  }
  &__legend {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 4;
    &-item {
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
      color: #1F5F5B;
      position: relative;
      padding-left: 18px;
      &::before {
        content: '';
        width: 10px;
        height: 10px;
        position: absolute;
        left: 0;
        border-radius: 50%;
        top: 0;
        bottom: 0;
        margin: auto 0;
        border: 1px solid #1F5F5B;
      }

      &:first-child {
        color: #FF473C;
        &::before {
          background: #FF473C;
        }
      }
      &:nth-child(2) {
        color: #FFBD70;
        &::before {
          background: #FFBD70;
        }
      }
      &:last-child {
        &::before {
          background: #ffffff;
        }
      }
    }
  }
}




.flat {
  &-unavailable {
    fill: #F9F9F9;
    fill-opacity: 0.95;
    // pointer-events: none;
  }
  &-booked {
    // fill: #F9F9F9;
    fill: #FFBD70;
    fill-opacity: 0.8;
    // pointer-events: none;
    position: relative;
  }
  &-sold {
    fill: #FF473C;
    fill-opacity: 0.94;
    pointer-events: none;
    position: relative;
  }
}


.flat-floors {
  margin-top: 10px;
  width: 100%;
  & span {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #159947;

    @include width-1400 {
      font-size: 18px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }

  &__items {
    margin-top: 8px;
    display: flex;

    @include width-960 {
      margin-left: -40px;
      margin-right: -40px;
      padding: 0 40px;
      overflow-x: scroll;
      /* Hide scrollbar for Chrome, Safari and Opera */
      &::-webkit-scrollbar {
        display: none;
      }
      /* Hide scrollbar for IE, Edge and Firefox */
      -ms-overflow-style: none;  /* IE and Edge */
      scrollbar-width: none;  /* Firefox */
      // width: 100%;
    }
    @include width-768 {
      margin-left: -20px;
      margin-right: -20px;
      padding: 0 20px;
    }
  }

  &__item {
    font-weight: 400;
    font-size: 20px;
    line-height: 24px;
    color: #159947;
    border: 1px solid #1F5F5B;
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: #159947;
      color: #ffffff;
      border: 1px solid #159947;
    }
    &.active {
      background: #1F5F5B;
      color: #ffffff;
      &:hover {
        border: 1px solid #1F5F5B;
      }
    }
    &.unavailable {
      color: #B4D0CE;
      border: 1px solid #B4D0CE;
      &:hover {
        background: none;
        cursor: not-allowed;
      }
    }

    @include width-1400 {
      height: 50px;
      font-size: 18px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-960 {
      width: 100%;
      max-width: 50px;
      flex-shrink: 0;
    }
    @include width-768 {
      height: 40px;
      max-width: 40px;
      font-size: 14px;
    }
  }
}
