.docs {
  margin-top: 50px;
  margin-bottom: 80px;
  @include width-1400 {
    margin-bottom: 70px;
  }
  @include width-1200 {
    margin-top: 40px;
    margin-bottom: 60px;
  }
  &__items {
  }
}

.docs-item {
  display: grid;
  align-items: center;
  grid-template-areas: 'icon title size btn';
  grid-template-columns: auto 1fr auto auto;
  padding: 16px 24px;
  border-bottom: 1px solid #B4D0CE;

  @include width-1200 {
    padding: 16px;
    grid-template-areas: 'icon title btn' 'icon size btn';
    grid-template-columns: auto 1fr auto;
  }
  @include width-768 {
    padding: 8px 0;
    grid-template-areas: 'icon title' 'icon size' 'btn btn';
  }
  &:not(:last-child) {
    margin-bottom: 20px;
    @include width-1200 {
      margin-bottom: 0;
    }
    @include width-768 {
      margin-bottom: 16px;
    }
  }
  &__icon {
    grid-area: icon;
    width: 60px;
    height: 60px;

    @include width-1400 {
      width: 50px;
      height: 50px;
    }
    @include width-1200 {
      width: 40px;
      height: 40px;
    }
    @include width-768 {
      width: 30px;
      height: 30px;
    }
  }

  &__title {
    grid-area: title;
    justify-self: flex-start;
    margin-left: 32px;
    margin-right: 32px;

    @include width-1200 {
      margin-left: 24px;
    }
    @include width-768 {
      margin-left: 10px;
      margin-right: 0;
    }
  }

  &__size {
    grid-area: size;
    font-weight: 300;
    font-size: 24px;
    line-height: 120%;
    font-feature-settings: 'pnum' on, 'lnum' on;
    color: #061A23;
    justify-self: center;
    margin-right: 110px;

    @include width-1400 {
      margin-right: 50px;
      font-size: 20px;
    }
    @include width-1200 {
      justify-self: flex-start;
      margin-left: 24px;
      font-size: 18px;
      margin-top: 4px;
    }
    @include width-768 {
      font-size: 16px;
      margin-left: 10px;
    }
  }

  &__btn {
    grid-area: btn;

    @include width-768 {
      justify-self: center;
      margin-top: 16px;
    }
    & svg {
      width: 20px;
      height: 20px;
      margin-left: 20px;
    }
  }
}
