.banks {
  & h2 {
    @include width-1200 {
      max-width: 680px;
      margin: 0 auto;
    }
  }
  &__items {
    margin-top: 50px;
  }

  &__line {
    display: grid;
    justify-content: center;
    gap: 32px;

    @include width-1200 {
      gap: 16px;
    }
    @include width-768 {
      gap: 8px;
    }
  }

  &__line-first {
    grid-template-columns: repeat(3,auto);
  }

  &__item {
    width: 100%;
    max-width: 326px;

    @include width-1200 {
      max-width: 210px;
    }
    @include width-768 {
      max-width: 106px;
    }
  }

  &__line-second {
    grid-template-columns: auto auto;
    margin-top: 32px;

    @include width-768 {
      margin-top: 20px;
    }
  }
}

.mort {
  margin-top: 60px;

  @include width-768 {
    margin-top: 40px;
  }
  &__body {
    display: grid;
    grid-template-areas: 'img title' 'img text';
    grid-template-columns: 1fr 1fr;
    gap: 32px;
    align-items: center;

    @include width-1200 {
      grid-template-areas: 'img title' 'text text';
      column-gap: 16px;
      row-gap: 30px;
    }
    @include width-768 {
      grid-template-columns: 1fr;
      grid-template-areas: 'img' 'title' 'text';
      row-gap: 20px;
    }
  }
  &__img {
    background: linear-gradient(90deg, #B4D0CE -11.92%, #4AB265 111.83%);
    grid-area: img;
  }
  &__title {
    grid-area: title;
    align-self: flex-end;

    @include width-1200 {
      align-self: center;
    }
  }

  &__text {
    grid-area: text;
    align-self: flex-start;
    font-weight: 300;
    font-size: 24px;
    line-height: 135%;
    color: #061A23;

    @include width-1400 {
      font-size: 22px;
    }
    @include width-1200 {
      font-size: 16px;
    }
    @include width-768 {
      font-size: 14px;
    }
  }
}

.mort-ways {
  margin-top: 80px;
  @include width-1200 {
    margin-top: 60px;
  }
  & .ways__title {
    font-weight: 275;
    font-size: 70px;
    line-height: 100%;
    max-width: 450px;
    align-self: flex-start;
    @include width-1400 {
      font-size: 60px;
      letter-spacing: -0.03em;
      max-width: 400px;
    }
    @include width-1200 {
      font-size: 40px;
      text-align: center;
      margin: 0 auto;
      max-width: 490px;
    }
    @include width-768 {
      font-size: 30px;
    }
  }
  & .ways__hat {
    @include width-1200 {
      flex-direction: column;
    }
  }
  & .ways__list {
    @include width-1400 {
      max-width: 660px;
    }
    @include width-1200 {
      max-width: 100%;
      margin-left: 0;
      margin-top: 32px;
    }
  }
  & .ways__list ul li {
    font-weight: 300;
    font-size: 28px;
    line-height: 125%;
    @include width-1400 {
      font-size: 26px;
    }
    @include width-1200 {
      font-size: 24px;
    }
    @include width-768 {
      font-size: 16px;
      line-height: 110%;
    }
  }
}
