.page404-container {
  max-width: 1020px;

  @include width-1400 {
    max-width: 870px;
  }
  @include width-1200 {
    max-width: 700px;
  }
}
.page404 {

		&__body {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 160px 0;

      @include width-1400 {
        padding: 140px 0;
      }
      @include width-1200 {
        padding: 120px 0;
      }
      @include width-768 {
        padding: 0;
        flex-direction: column;
        margin-top: 60px;
        margin-bottom: 60px;
      }
		}
    &__btn {
      margin-top: 80px;
      max-width: 100%;

      @include width-1400 {
        margin-top: 70px;
      }
      @include width-1200 {
        margin-top: 60px;
      }
      @include width-768 {
        margin-top: 40px;
      }
    }

		&__img {
      width: 100%;
      margin-right: 16px;
      max-width: 565px;

      @include width-1400 {
        max-width: 430px;
      }
      @include width-1200 {
        max-width: 330px;
      }
      @include width-768 {
        margin-right: 0;
      }
		}

		&__text {
      width: 100%;
      max-width: 300px;

      @include width-768 {
        max-width: 400px;
      }
		}

		&__title {
      display: block;
      font-weight: 300;
      font-size: 90px;
      line-height: 130%;
      letter-spacing: -0.03em;
      text-transform: uppercase;
      color: #000000;

      @include width-1400 {
        font-size: 80px;
      }
      @include width-1200 {
        font-size: 70px;
      }
      @include width-768 {
        font-size: 45px;
        margin-top: 40px;
        text-align: center;
      }
		}

		&__subtitle {
      display: block;
      font-weight: 300;
      font-size: 28px;
      line-height: 125%;
      color: #000000;
      margin-top: 16px;
      @include width-1400 {
        font-size: 26px;
      }
      @include width-1200 {
        font-size: 24px;
        margin-top: 8px;
      }
      @include width-768 {
        font-size: 20px;
        text-align: center;
      }
		}
}
