.con-intro {
  &__title {
    font-weight: 275;
    font-size: 70px;
    line-height: 100%;
    text-align: center;
    color: #061A23;
    letter-spacing: -0.03em;

    @include width-1400 {
      font-size: 60px;
    }
    @include width-1200 {
      font-size: 40px;
    }
    @include width-768 {
      font-size: 30px;
    }
  }
  &__container {
    @include width-768 {
      padding: 0;
    }
  }
  &__body {
    margin-top: 100px;
    height: 470px;
    display: flex;
    align-items: flex-end;
    padding: 0 30px;
    background: linear-gradient(90deg, #B4D0CE -11.92%, #4AB265 111.83%);

    @include width-1400 {
      height: 320px;
      padding-right: 50px;
      justify-content: space-between;
    }
    @include width-1200 {
      margin-top: 40px;
      height: auto;
      flex-direction: column;
      align-items: center;
      padding: 0 20px;
      padding-top: 30px;
    }
  }

  &__img {
    position: relative;

    @include width-1200 {
      order: 1;
      margin-top: 20px;
      z-index: 1;
    }
    & picture {
      & img {
        width: 100%;
        max-width: 726px;

        @include width-1700 {
          max-width: 600px;
        }
        @include width-1400 {
          max-width: 450px;
        }
        @include width-1200 {
          max-width: 350px;
          justify-self: flex-end;
        }
      }
    }
    &-icon {
      width: 140px;
      height: 140px;
      position: absolute;
      left: 0;
      top: 200px;

      @include width-1700 {
        width: 80px;
        height: 80px;
      }
      @include width-1400 {
        top: 140px;
        left: -10px;
      }
      @include width-1200 {
        width: 100px;
        height: 100px;
        top: 40px;
        left: -40px;
      }
      @include width-768 {
        width: 80px;
        height: 80px;
        left: -10px;
      }
    }
  }

  &__text {
    align-self: center;
    max-width: 800px;
    font-weight: 300;
    font-size: 45px;
    line-height: 115%;
    color: #1F5F5B;
    margin-left: 40px;
    margin-right: 120px;
    position: relative;
    &::after {
      content: '';
      position: absolute;
      width: 565px;
      height: 35px;
      bottom: -50px;
      left: 50px;
      background: url('../img/icons/con-intro_1.svg') no-repeat;

      @include width-1400 {
        left: 0;
        width: 100%;
      }
      @include width-768 {
        background-size: 100%;
      }
    }
    &::before {
      content: '';
      position: absolute;
      width: 80px;
      height: 70px;
      right: 0;
      top: -100px;
      background: url('../img/icons/con-intro_2.svg') no-repeat;
      background-size: 80px;

      @include width-1400 {
        width: 70px;
        height: 60px;
        background-size: 70px;
        top: -50px;
        right: -40px;
      }
      @include width-1200 {
        width: 37px;
        height: 34px;
        background-size: 37px;
        top: -20px;
        right: 0;
      }
      @include width-768 {
        right: -15px;
        top: -25px;
      }
    }

    & span {
      color: #ffffff;
    }

    @include width-1700 {
      font-size: 40px;
      max-width: 700px;
      margin-right: 0;
    }
    @include width-1400 {
      font-size: 36px;
      max-width: 600px;
      margin-left: 15px;
    }
    @include width-1200 {
      order: 0;
      font-size: 28px;
      margin-left: 0;
      max-width: 530px;
      text-align: center;
    }
    @include width-768 {
      font-size: 24px;
      max-width: 400px;
    }
  }
}

.con-stat {
  margin-top: 50px;

  @include width-1200 {
    margin-top: 40px;
  }
  &__item {
    & >:first-child {
      font-size: 45px;
      line-height: 115%;
      margin-bottom: 16px;

      @include width-1400 {
        font-size: 36px;
      }
      @include width-1200 {
        font-size: 28px;
      }
      @include width-768 {
        font-size: 24px;
      }
    }
    & >:last-child {
      font-size: 28px;
      line-height: 125%;

      @include width-1400 {
        font-size: 26px;
      }
      @include width-1200 {
        font-size: 24px;
      }
      @include width-768 {
        font-size: 20px;
      }
    }
  }
}

.con-struct {
  margin-top: 80px;

  @include width-1400 {
    margin-top: 70px;
  }
  @include width-1200 {
    margin-top: 60px;
  }
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 32px;

    @include width-1200 {
      grid-template-columns: 1fr;
      grid-template-rows: auto;
      gap: 16px;
    }
  }

  &__card {
    background: #F9F9F9;
    display: flex;
    flex-direction: column;

    @include width-1200 {
      flex-direction: row;
      align-items: center;
    }
    @include width-768 {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__card-first {
    grid-row: span 2;

    & .con-struct__text {
      padding: 40px;
      padding-bottom: 30px;
      max-width: 380px;

      @include width-1400 {
        padding: 32px;
      }
      @include width-1200 {
        padding: 0;
        padding-left: 18px;
      }
      & span {
        display: block;
        margin-top: 8px;
      }
    }

    @include width-1200 {
      grid-row: span 1;
      & .con-struct__text {
        order: 1;
      }
    }
    @include width-768 {
      & .con-struct__text {
        order: 0;
        padding: 16px;
      }
    }
  }
  &__img {
    margin-top: auto;
    position: relative;

    @include width-1200 {
      max-width: 330px;
    }

    &-abs {
      position: absolute;
      right: 20px;
      top: -50px;
      width: 120px;
      height: 120px;

      @include width-1400 {
        width: 100px;
        height: 100px;
        right: 50px;
        top: -100px;
      }
      @include width-1200 {
        width: 70px;
        height: 70px;
        right: -20px;
        top: -30px;
      }
      @include width-768 {
        width: 55px;
        height: 55px;
        right: 30px;
        top: -10px;
      }
    }
  }
  &__card-second {
    padding: 40px;

    @include width-1400 {
      padding: 32px;
    }
    @include width-768 {
      padding: 16px 16px 24px;
    }
    & img {
      width: 100%;
      max-width: 190px;

      @include width-1200 {
        max-width: 140px;
        margin-right: 70px;
      }
      @include width-768 {
        max-width: 100px;
      }
    }
    & .con-struct__text {
      margin-top: auto;
      @include width-1400 {
        margin-top: 30px;
      }
      @include width-1200 {
        margin-top: 0;
      }
      @include width-768 {
        margin-top: 16px;
      }
    }
  }
  &__card-third {
    padding: 40px;

    @include width-1400 {
      padding: 32px;
    }
    @include width-768 {
      padding: 16px 16px 24px;
    }
    & img {
      width: 100%;
      max-width: 525px;
      @include width-1200 {
        max-width: 140px;
        margin-right: 70px;
      }
      @include width-768 {
        max-width: 220px;
      }
    }
    & .con-struct__text {
      margin-top: auto;

      @include width-1400 {
        margin-top: 30px;
      }
      @include width-1200 {
        margin-top: 0;
      }
      @include width-768 {
        margin-top: 16px;
      }
    }
  }

  &__text {
    & h2 {
      text-align: left;
    }
    & span {
      font-weight: 300;
      font-size: 28px;
      line-height: 125%;

      @include width-1400 {
        font-size: 26px;
      }
      @include width-1200 {
        font-size: 24px;
      }
      @include width-768 {
        font-size: 20px;
      }
    }
  }
}

.con-builder {
  margin-top: 50px;
  @include width-1200 {
    margin-top: 40px;
  }
}

.con-value {
  margin-top: 50px;
  margin-bottom: 130px;

  @include width-1400 {
    margin-top: 40px;
    margin-bottom: 70px;
  }
  @include width-1200 {
    margin-bottom: 60px;
  }
  &__title {
    color: #1F5F5B;
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3,1fr);
    margin-top: 50px;

    @include width-1200 {
      margin-top: 40px;
    }
    @include width-768 {
      grid-template-columns: 1fr;
      margin-top: 32px;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    & img {
      width: 180px;
      height: 180px;

      @include width-1400 {
        width: 160px;
        height: 160px;
      }
      @include width-1200 {
        width: 120px;
        height: 120px;
      }
    }

    @include width-768 {
      &:not(:first-child) {
        margin-top: 50px;
      }
    }
  }
  &__item-title {
    color: #1F5F5B;
    margin-top: 25px;
    font-weight: 300;

    @include width-1400 {
      max-width: 280px;
      text-align: center;
    }
    @include width-1200 {
      max-width: 210px;
    }
    @include width-768 {
      max-width: 100%;
    }
  }
}


.con-pride {
  margin-top: 250px;
  margin-bottom: 80px;
  @include width-1400 {
    margin-top: 180px;
    margin-bottom: 70px;
  }
  @include width-1200 {
    margin-top: 60px;
    margin-bottom: 60px;
  }

  &__container {
    @include width-1200 {
      padding: 0;
      overflow-x: hidden;
    }
  }
  &__body {
    background: linear-gradient(90deg, #4AB265 -11.92%, #B4D0CE 111.83%);
    height: 480px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    position: relative;

    @include width-1400 {
      height: 420px;
    }
    @include width-1200 {
      height: 365px;
    }
    @include width-768 {
      flex-direction: column;
      height: auto;
      align-items: center;
    }
  }

  &__text {
    font-weight: 200;
    font-size: 70px;
    line-height: 100%;
    color: #FFFFFF;
    max-width: 900px;
    position: relative;
    z-index: 1;
    align-self: center;
    margin-left: 200px;
    &::after {
      content: '';
      position: absolute;
      width: 92px;
      height: 92px;
      left: -50px;
      top: -100px;
      background: url('../img/icons/star.svg') no-repeat;

      @include width-1400 {
        width: 63px;
        height: 63px;
        background-size: 63px;
        left: -30px;
        top: -80px;
      }
      @include width-768 {
        width: 53px;
        height: 53px;
        background-size: 53px;
        top: -60px;
        left: 30px;
      }
    }

    @include width-1700 {
      margin-left: 100px;
      font-size: 60px;
      max-width: 580px;
    }
    @include width-1400 {
      margin-left: 40px;
    }
    @include width-1200 {
      font-size: 40px;
      max-width: 470px;
      letter-spacing: -0.03em;
    }
    @include width-768 {
      font-size: 30px;
      max-width: 380px;
      text-align: center;
      margin-top: 80px;
      margin-left: 0;
    }
  }

  &__img {
    max-width: 510px;
    position: relative;
    margin-right: 100px;
    &::after {
      content: '';
      position: absolute;
      width: 255px;
      height: 152px;
      right: -80px;
      top: 30px;
      background: url('../img/pages/constructor/highlight.svg') no-repeat;
      z-index: 2;

      @include width-1400 {
        width: 225px;
        height: 134px;
        background-size: 225px 134px;
        right: -50px;
      }
      @include width-1200 {
        width: 140px;
        height: 84px;
        background-size: 140px 84px;
        top: 10px;
      }
      @include width-768 {
        top: 30px;
        right: -40px;
      }
    }

    @include width-1400 {
      margin-right: 80px;
      max-width: 420px;
    }
    @include width-1200 {
      max-width: 260px;
      margin-right: 70px;
    }
    @include width-768 {
      margin-right: 0;
      margin-top: 10px;
      max-width: 300px;
    }
  }
}
